<script>
import axios from 'axios';

export default {
  data() {
    return {
      formData: {
        name: '',
        email: '',
        message: ''
      },
      appointmentData: {
        name: '',
        phone: '',
        email: '',
        time: '',
        service: '',
        message: ''
      },
      responseMessage: ''
    };
  },
  methods: {
    async submitForm() {
      console.log(this.formData)
      try {

        const response = await axios.post('https://floraisondental.alruya.link/admin/contact.php', this.formData, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        this.responseMessage = response.data.message;

        // Redirect to the thank you page
        this.$router.push('/thankyou2');
      } catch (error) {
        this.responseMessage = 'An error occurred. Please try again.';
      }
    },
    async appointmentForm() {
      console.log(this.formData)
      try {

        const response = await axios.post('https://floraisondental.alruya.link/admin/appointment.php', this.appointmentData, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        this.responseMessage = response.data.message;

        // Redirect to the thank you page
        this.$router.push('/thankyou');
      } catch (error) {
        this.responseMessage = 'An error occurred. Please try again.';
      }
    }

  }
};
</script>

