import Vue from 'vue';
import VueRouter from 'vue-router';
import HomeView from '../views/HomeView.vue';
import ThankYou from '../views/ThankYou.vue';
import ThankYou2 from '../views/ThankYou2.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: { title: 'Home' }
  },
  {
    path: '/thankyou',
    name: 'thankyou',
    component: ThankYou,
    meta: { title: 'Thank You Page' }
  },
  {
    path: '/thankyou2',
    name: 'thankyou2',
    component: ThankYou2,
    meta: { title: 'Thank You Page 2' }
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = `${to.meta.title} - Floraisondental`;
  } else {
    document.title = 'Floraisondental';
  }
  next();
});

export default router;
